/**
 * Engram API 
 */

// ==================== LLM  ====================

/**
 * LLM 
 */
export interface LLMPreset {
  /**  */
  id: string;
  /**  */
  name: string;
  /**  connection_profile  */
  source: 'tavern' | 'tavern_profile' | 'custom';
  /**  connection_profile ID source === 'tavern_profile'  */
  tavernProfileId?: string;
  /**  API  source === 'custom'  */
  custom?: CustomAPIConfig;
  /**  */
  parameters: SamplingParameters;
  /**  */
  context: ContextSettings;
  /**  */
  isDefault?: boolean;
  /**  */
  createdAt: number;
  /**  */
  updatedAt: number;
}


/**
 *  API 
 */
export interface CustomAPIConfig {
  /** API  URL */
  apiUrl: string;
  /** API Key */
  apiKey: string;
  /**  */
  model: string;
  /** API / */
  apiSource: APISource;
}

/**
 *  API 
 */
export type APISource = 'openai' | 'anthropic' | 'ollama' | 'vllm' | 'azure' | 'custom';

/**
 * 
 */
export interface SamplingParameters {
  /**  (0-2) */
  temperature: number;
  /** Top-P  (0-1) */
  topP: number;
  /**  tokens */
  maxTokens: number;
  /**  (-2  2) */
  frequencyPenalty: number;
  /**  (-2  2) */
  presencePenalty: number;
}

/**
 * 
 */
export interface ContextSettings {
  /**  (-1 ) */
  maxChatHistory: number;
}

// ====================  ====================

/**
 * 
 */
export interface VectorConfig {
  /**  */
  source: VectorSource;
  /** API  */
  apiUrl?: string;
  /** API Key */
  apiKey?: string;
  /**  */
  model?: string;
  /**  */
  dimensions?: number;
}

/**
 * 
 */
export type VectorSource =
  | 'transformers'  //  transformers
  | 'openai'        // OpenAI Embeddings API
  | 'ollama'        // Ollama
  | 'vllm'          // vLLM
  | 'cohere'        // Cohere
  | 'jina'          // Jina AI
  | 'voyage';       // Voyage AI

// ==================== Rerank  ====================

/**
 * Rerank 
 */
export interface RerankConfig {
  /**  */
  enabled: boolean;
  /** API  */
  url: string;
  /** API Key */
  apiKey: string;
  /**  */
  model: string;
  /**  */
  topN: number;
  /**  (0-1, 0=, 1=Rerank) */
  hybridAlpha: number;
}

// ====================  ====================

/**
 * 
 */
export type PromptCategory =
  | 'text_summary'      // 
  | 'vector_summary'    // 
  | 'trim'              // /
  | 'query_enhance';    // 

/**
 * 
 */
export const PROMPT_CATEGORIES: { value: PromptCategory; label: string; description: string }[] = [
  { value: 'text_summary', label: '', description: '' },
  { value: 'vector_summary', label: '', description: ' JSON/' },
  { value: 'trim', label: '/', description: '' },
  { value: 'query_enhance', label: '', description: '' },
];

/**
 * 
 */
export interface PromptTemplate {
  /**  */
  id: string;
  /**  */
  name: string;
  /**  */
  category: PromptCategory;
  /**  */
  enabled: boolean;
  /**  */
  isBuiltIn: boolean;
  /**  LLM  IDnull  */
  boundPresetId: string | null;
  /**  */
  systemPrompt: string;
  /**  {{chatHistory}}, {{context}}  */
  userPromptTemplate: string;
  /**  */
  outputFormat: 'json' | 'markdown' | 'plain';
  /**  */
  availableVariables: string[];
  /**  */
  createdAt: number;
  /**  */
  updatedAt: number;
}

/**
 * 
 */
export interface PromptTemplateSingleExport {
  version: string;
  exportedAt: number;
  template: Omit<PromptTemplate, 'id' | 'isBuiltIn' | 'enabled' | 'createdAt' | 'updatedAt'>;
}

/**
 * 
 */
export interface PromptTemplateExport {
  version: string;
  exportedAt: number;
  templates: Omit<PromptTemplate, 'id' | 'isBuiltIn' | 'enabled' | 'createdAt' | 'updatedAt'>[];
}

// ====================  ====================

/**
 * 
 */
export interface WorldbookConfig {
  /**  */
  enabled: boolean;
  /** false  */
  includeGlobal: boolean;
}

// ====================  ====================

/**
 * 
 */
export type TrimTriggerType = 'token' | 'count';

/**
 * 
 */
export interface TrimConfig {
  /**  */
  enabled: boolean;
  /**  */
  trigger: TrimTriggerType;
  /** Token trigger='token'  */
  tokenLimit: number;
  /** trigger='count'  */
  countLimit: number;
  /**  N  */
  keepRecentCount?: number;
  /**  */
  preserveOriginal?: boolean;
  /**  */
  previewEnabled?: boolean;
}

// ====================  ====================

/**
 * Engram API 
 */
export interface EngramAPISettings {
  /** LLM  */
  llmPresets: LLMPreset[];
  /**  LLM  ID */
  selectedPresetId: string | null;
  /**  */
  vectorConfig: VectorConfig;
  /** Rerank  */
  rerankConfig: RerankConfig;
  /**  */
  promptTemplates: PromptTemplate[];
  /**  */
  worldbookConfig: WorldbookConfig;
  /**  */
  trimConfig?: TrimConfig;
}

